.toplevel_page_wp-react-form #wpcontent {
  padding-left: 0px;
}

.wp-react-form.wprf-tabs-wrapper {
  display: flex;
  flex-direction: column;
  margin: 20px;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-menu-wrapper {
  margin-bottom: 5px;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-menu-wrapper .wprf-tab-nav {
  margin: 0;
  padding: 0;
  display: flex;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-menu-wrapper .wprf-tab-nav .wprf-tab-nav-item {
  padding: 15px 25px;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 0;
  background-color: #f8fafb;
  color: #516378;
  border-top: 2px solid #f8fafb;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-menu-wrapper .wprf-tab-nav .wprf-tab-nav-item.wprf-active-nav {
  border-color: #6648fe;
  background-color: #FFF;
  color: #516378;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-menu-wrapper.wprf-tab-menu-sidebar {
  flex-basis: 15%;
  background-color: #f8fafb;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-menu-wrapper.wprf-tab-menu-sidebar .wprf-tab-nav {
  display: block;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-menu-wrapper.wprf-tab-menu-sidebar .wprf-tab-nav .wprf-tab-nav-item.wprf-active-nav {
  border-top-width: 0px;
  border-right: 2px solid #6648fe;
}
.wp-react-form.wprf-tabs-wrapper.wprf-tab-menu-as-sidebar {
  border: 1px solid #f2f2f2;
  flex-direction: row;
  background-color: #FFF;
  padding: 0px;
}
.wp-react-form.wprf-tabs-wrapper.wprf-tab-menu-as-sidebar .wprf-tab-menu-wrapper {
  margin-bottom: 0;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-content-wrapper {
  flex: 1;
  flex-basis: 85%;
  background: #FFF;
  padding: 30px;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-content-wrapper .wprf-tab-content {
  display: none;
}
.wp-react-form.wprf-tabs-wrapper .wprf-tab-content-wrapper .wprf-tab-content.wprf-active {
  display: block;
}
.wp-react-form .wprf-control {
  margin-bottom: 20px;
}
.wp-react-form .wprf-control:last-of-type {
  margin-bottom: 0px;
}
.wp-react-form .wprf-control .wprf-control-label {
  margin-top: 0;
  margin-bottom: 0;
}
.wp-react-form .wprf-control .wprf-input-field {
  box-shadow: none;
  border: 1px solid #f2f2f2;
  transition: border linear 0.2s, box-shadow linear 0.2s;
  padding: 8px;
  line-height: 1;
  font-size: 14px;
  margin: 0px;
}
.wp-react-form .wprf-control .wprf-input-field.wprf-large {
  width: 100%;
}
.wp-react-form .wprf-control .wprf-input-field:focus {
  box-shadow: none;
  outline: none;
}
.wp-react-form .wprf-control .clearfix::after, .wp-react-form .wprf-control .clearfix::before {
  display: table;
  content: "";
}
.wp-react-form .wprf-control .clearfix::after {
  clear: both;
}
.wp-react-form .wprf-control .wprf-input-label {
  display: block;
  margin-bottom: 10px;
}
.wp-react-form .wprf-control .wprf-input-label .wprf-label-image {
  max-width: 100%;
}
.wp-react-form .wprf-control .wprf-row {
  margin-left: -10px;
  margin-right: -10px;
}
.wp-react-form .wprf-control .wprf-row .wprf-column {
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  width: 20%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-1 {
  width: 8.3333%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-2 {
  width: 16.6667%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-3 {
  width: 25%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-4 {
  width: 33.3333%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-5 {
  width: 41.6667%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-6 {
  width: 50%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-7 {
  width: 58.3333%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-8 {
  width: 66.6667%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-9 {
  width: 75%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-10 {
  width: 83.3333%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-11 {
  width: 91.6667%;
}
.wp-react-form .wprf-control .wprf-row .wprf-column.wprf-column-12 {
  width: 100%;
}
.wp-react-form .wprf-control .wprf-flex {
  display: flex;
  flex-wrap: wrap;
}
.wp-react-form .wprf-control .wprf-error-message {
  color: red;
}